.App {
  display: flex;
  justify-content: center;
  margin-top: 5vh;
}

#display {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
  font-size: 60px;
  border: 1px solid;
  margin: 10px;
  box-shadow: inset 0 0 20px #999;
  height: 60px;
}

.drum-pads {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.drum-pad {
  border: 1px solid black;
  margin: 10px;
  border-radius: 5px;
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 5px 5px 5px 2px lightgray;
  background: #666;
  color: white;
  font-size: 30px;
  font-weight: bold;
}